"use client"

import { Header } from "@/components/header"
import { Footer } from "@/components/footer"
import { WhatsAppButton } from "@/components/whatsapp-button"
import { CheckCircle2, Lock, ShieldCheck, Home, Briefcase, MapPin, Plus } from "lucide-react"
import Link from "next/link"
import { useState, useEffect } from "react"
import { useAuth } from "@/lib/auth-context"
import { mockUsers } from "@/lib/mock-data"

export default function CheckoutPage() {
  const { user } = useAuth()
  const [step, setStep] = useState(1)
  const [selectedAddressId, setSelectedAddressId] = useState<string | null>(null)
  
  // Ensure we get the latest user data including addresses, even if session is stale
  const dbUser = user ? mockUsers.find(u => u.id === user.id) : null
  const currentUser = dbUser || mockUsers[1] // Fallback to Maria
  const userAddresses = currentUser.addresses || []

  const [formData, setFormData] = useState({
    email: currentUser.email || "",
    firstName: currentUser.nome || "",
    lastName: currentUser.sobrenome || "",
    phone: currentUser.celular || "",
    address: "",
    city: "",
    state: "",
    zipCode: "",
    cardName: "",
    cardNumber: "",
    cardExpiry: "",
    cardCVC: "",
  })

  const handleAddressSelect = (addressId: string) => {
    setSelectedAddressId(addressId)
    
    if (addressId === "new") {
      setFormData({
        ...formData,
        address: "",
        city: "",
        state: "",
        zipCode: "",
      })
    } else {
      const selected = userAddresses.find(a => a.id === addressId)
      if (selected) {
        setFormData({
          ...formData,
          firstName: selected.firstName,
          lastName: selected.lastName,
          phone: selected.phone,
          address: selected.address,
          city: selected.city,
          state: selected.state,
          zipCode: selected.zipCode,
        })
      }
    }
  }

  const subtotal = 219.8
  const shipping = 0
  const total = subtotal + shipping

  return (
    <div className="min-h-screen flex flex-col">
      <Header />

      <div className="flex-1 py-12 px-4">
        <div className="max-w-6xl mx-auto">
          {/* Step Indicator */}
          <div className="mb-12">
            <div className="flex items-center justify-between mb-8">
              {[
                { num: 1, label: "Carrinho" },
                { num: 2, label: "Endereço" },
                { num: 3, label: "Pagamento" },
                { num: 4, label: "Confirmação" },
              ].map((s) => (
                <div key={s.num} className="flex flex-col items-center flex-1">
                  <div
                    className={`w-10 h-10 rounded-full flex items-center justify-center font-medium mb-2 transition-colors ${
                      s.num < step
                        ? "bg-accent text-accent-foreground"
                        : s.num === step
                          ? "bg-primary text-primary-foreground"
                          : "bg-muted text-foreground/50"
                    }`}
                  >
                    {s.num < step ? <CheckCircle2 size={20} /> : s.num}
                  </div>
                  <span className={`text-xs font-medium ${s.num <= step ? "text-foreground" : "text-foreground/50"}`}>
                    {s.label}
                  </span>
                  {s.num < 4 && <div className={`h-0.5 w-full mt-4 ${s.num < step ? "bg-accent" : "bg-border"}`} />}
                </div>
              ))}
            </div>
          </div>

          <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
            {/* Checkout Form */}
            <div className="lg:col-span-2">
              {/* Step 1: Cart Review */}
              {step === 1 && (
                <div className="space-y-6">
                  <h2 className="text-2xl font-light text-foreground">Revise seu carrinho</h2>

                  <div className="space-y-4">
                    {[1, 2].map((item) => (
                      <div key={item} className="flex gap-4 p-4 border border-border rounded-lg">
                        <img
                          src={`/placeholder.svg?height=80&width=80&text=Produto ${item}`}
                          alt={`Produto ${item}`}
                          className="w-20 h-20 object-cover rounded-lg"
                        />
                        <div className="flex-1">
                          <h3 className="font-medium text-foreground">Produto Premium {item}</h3>
                          <p className="text-sm text-foreground/60">Personalização: Texto customizado</p>
                          <div className="flex justify-between items-center mt-2">
                            <span className="text-sm">Quantidade: {item}</span>
                            <span className="font-medium">R$ {(129.9 * item).toFixed(2)}</span>
                          </div>
                        </div>
                      </div>
                    ))}
                  </div>

                  <button
                    onClick={() => setStep(2)}
                    className="w-full bg-primary text-primary-foreground py-3 rounded-lg font-medium hover:bg-primary/90 transition-colors"
                  >
                    Continuar para Endereço
                  </button>
                </div>
              )}

              {/* Step 2: Shipping Address */}
              {step === 2 && (
                <div className="space-y-6">
                  <h2 className="text-2xl font-light text-foreground">Endereço de Entrega</h2>

                  {/* Saved Addresses Grid */}
                  <div className="grid grid-cols-1 sm:grid-cols-3 gap-4 mb-6">
                    {userAddresses.map((addr) => (
                      <button
                        key={addr.id}
                        onClick={() => handleAddressSelect(addr.id)}
                        className={`p-4 border rounded-xl text-left transition-all ${
                          selectedAddressId === addr.id
                            ? "border-accent bg-accent/5 ring-1 ring-accent"
                            : "border-border hover:border-accent/50 hover:bg-muted/50"
                        }`}
                      >
                        <div className="flex items-center gap-2 mb-2">
                          {addr.label === "Casa" ? (
                            <Home size={18} className={selectedAddressId === addr.id ? "text-accent" : "text-foreground/60"} />
                          ) : addr.label === "Trabalho" ? (
                            <Briefcase size={18} className={selectedAddressId === addr.id ? "text-accent" : "text-foreground/60"} />
                          ) : (
                            <MapPin size={18} className={selectedAddressId === addr.id ? "text-accent" : "text-foreground/60"} />
                          )}
                          <span className={`font-medium ${selectedAddressId === addr.id ? "text-accent" : "text-foreground"}`}>
                            {addr.label}
                          </span>
                        </div>
                        <p className="text-sm text-foreground/70 line-clamp-2">
                          {addr.address}
                        </p>
                        <p className="text-xs text-foreground/50 mt-1">
                          {addr.city}/{addr.state}
                        </p>
                      </button>
                    ))}

                    {/* New Address Button */}
                    <button
                      onClick={() => handleAddressSelect("new")}
                      className={`p-4 border rounded-xl flex flex-col items-center justify-center gap-2 transition-all ${
                        selectedAddressId === "new"
                          ? "border-accent bg-accent/5 ring-1 ring-accent"
                          : "border-border border-dashed hover:border-accent/50 hover:bg-muted/50"
                      }`}
                    >
                      <div className={`w-8 h-8 rounded-full flex items-center justify-center ${
                        selectedAddressId === "new" ? "bg-accent text-accent-foreground" : "bg-muted text-foreground/60"
                      }`}>
                        <Plus size={18} />
                      </div>
                      <span className={`text-sm font-medium ${selectedAddressId === "new" ? "text-accent" : "text-foreground/70"}`}>
                        Outro Endereço
                      </span>
                    </button>
                  </div>

                  <div className="space-y-4">
                    <div className="grid grid-cols-2 gap-4">
                      <input
                        type="text"
                        placeholder="Nome"
                        className="col-span-1 px-4 py-3 border border-border rounded-lg bg-background text-foreground placeholder-foreground/50"
                        value={formData.firstName}
                        onChange={(e) => setFormData({ ...formData, firstName: e.target.value })}
                      />
                      <input
                        type="text"
                        placeholder="Sobrenome"
                        className="col-span-1 px-4 py-3 border border-border rounded-lg bg-background text-foreground placeholder-foreground/50"
                        value={formData.lastName}
                        onChange={(e) => setFormData({ ...formData, lastName: e.target.value })}
                      />
                    </div>

                    <input
                      type="email"
                      placeholder="Email"
                      className="w-full px-4 py-3 border border-border rounded-lg bg-background text-foreground placeholder-foreground/50"
                      value={formData.email}
                      onChange={(e) => setFormData({ ...formData, email: e.target.value })}
                    />

                    <input
                      type="tel"
                      placeholder="Telefone"
                      className="w-full px-4 py-3 border border-border rounded-lg bg-background text-foreground placeholder-foreground/50"
                      value={formData.phone}
                      onChange={(e) => setFormData({ ...formData, phone: e.target.value })}
                    />

                    <input
                      type="text"
                      placeholder="Rua, número e complemento"
                      className="w-full px-4 py-3 border border-border rounded-lg bg-background text-foreground placeholder-foreground/50"
                      value={formData.address}
                      onChange={(e) => setFormData({ ...formData, address: e.target.value })}
                    />

                    <div className="grid grid-cols-3 gap-4">
                      <input
                        type="text"
                        placeholder="CEP"
                        className="col-span-1 px-4 py-3 border border-border rounded-lg bg-background text-foreground placeholder-foreground/50"
                        value={formData.zipCode}
                        onChange={(e) => setFormData({ ...formData, zipCode: e.target.value })}
                      />
                      <input
                        type="text"
                        placeholder="Cidade"
                        className="col-span-1 px-4 py-3 border border-border rounded-lg bg-background text-foreground placeholder-foreground/50"
                        value={formData.city}
                        onChange={(e) => setFormData({ ...formData, city: e.target.value })}
                      />
                      <input
                        type="text"
                        placeholder="Estado"
                        className="col-span-1 px-4 py-3 border border-border rounded-lg bg-background text-foreground placeholder-foreground/50"
                        value={formData.state}
                        onChange={(e) => setFormData({ ...formData, state: e.target.value })}
                      />
                    </div>
                  </div>

                  <div className="flex gap-4">
                    <button
                      onClick={() => setStep(1)}
                      className="flex-1 border-2 border-primary text-primary py-3 rounded-lg font-medium hover:bg-primary/5 transition-colors"
                    >
                      Voltar
                    </button>
                    <button
                      onClick={() => setStep(3)}
                      className="flex-1 bg-primary text-primary-foreground py-3 rounded-lg font-medium hover:bg-primary/90 transition-colors"
                    >
                      Ir para Pagamento
                    </button>
                  </div>
                </div>
              )}

              {/* Step 3: Payment */}
              {step === 3 && (
                <div className="space-y-6">
                  <h2 className="text-2xl font-light text-foreground">Informações de Pagamento</h2>

                  <div className="flex items-center space-x-2 p-3 bg-muted rounded-lg text-sm">
                    <ShieldCheck size={18} className="text-accent" />
                    <span className="text-foreground/70">Suas informações de pagamento são seguras e protegidas</span>
                  </div>

                  <div className="space-y-4">
                    <input
                      type="text"
                      placeholder="Nome no Cartão"
                      className="w-full px-4 py-3 border border-border rounded-lg bg-background text-foreground placeholder-foreground/50"
                      value={formData.cardName}
                      onChange={(e) => setFormData({ ...formData, cardName: e.target.value })}
                    />

                    <input
                      type="text"
                      placeholder="Número do Cartão"
                      className="w-full px-4 py-3 border border-border rounded-lg bg-background text-foreground placeholder-foreground/50"
                      value={formData.cardNumber}
                      onChange={(e) => setFormData({ ...formData, cardNumber: e.target.value })}
                    />

                    <div className="grid grid-cols-2 gap-4">
                      <input
                        type="text"
                        placeholder="MM/YY"
                        className="px-4 py-3 border border-border rounded-lg bg-background text-foreground placeholder-foreground/50"
                        value={formData.cardExpiry}
                        onChange={(e) => setFormData({ ...formData, cardExpiry: e.target.value })}
                      />
                      <input
                        type="text"
                        placeholder="CVC"
                        className="px-4 py-3 border border-border rounded-lg bg-background text-foreground placeholder-foreground/50"
                        value={formData.cardCVC}
                        onChange={(e) => setFormData({ ...formData, cardCVC: e.target.value })}
                      />
                    </div>
                  </div>

                  <div className="space-y-3 p-4 bg-muted rounded-lg">
                    <h3 className="font-medium text-foreground text-sm">Métodos de Pagamento Aceitos</h3>
                    <div className="flex gap-2 flex-wrap">
                      {["Cartão de Crédito", "Cartão de Débito", "Mercado Pago", "PIX"].map((method) => (
                        <span
                          key={method}
                          className="text-xs bg-background px-2 py-1 rounded border border-border text-foreground/70"
                        >
                          {method}
                        </span>
                      ))}
                    </div>
                  </div>

                  <div className="flex gap-4">
                    <button
                      onClick={() => setStep(2)}
                      className="flex-1 border-2 border-primary text-primary py-3 rounded-lg font-medium hover:bg-primary/5 transition-colors"
                    >
                      Voltar
                    </button>
                    <button
                      onClick={() => setStep(4)}
                      className="flex-1 bg-primary text-primary-foreground py-3 rounded-lg font-medium hover:bg-primary/90 transition-colors flex items-center justify-center space-x-2"
                    >
                      <Lock size={18} />
                      <span>Confirmar Pedido</span>
                    </button>
                  </div>
                </div>
              )}

              {/* Step 4: Confirmation */}
              {step === 4 && (
                <div className="text-center space-y-6">
                  <div className="flex justify-center">
                    <div className="w-20 h-20 bg-accent rounded-full flex items-center justify-center">
                      <CheckCircle2 size={40} className="text-accent-foreground" />
                    </div>
                  </div>

                  <div>
                    <h2 className="text-3xl font-light text-foreground mb-2">Pedido Confirmado!</h2>
                    <p className="text-foreground/70">Obrigado pela sua compra</p>
                  </div>

                  <div className="bg-muted p-6 rounded-lg text-left space-y-3">
                    <p className="text-sm text-foreground/70">
                      <span className="font-medium text-foreground">Número do Pedido:</span> MEM-20250127-001
                    </p>
                    <p className="text-sm text-foreground/70">
                      <span className="font-medium text-foreground">Email de Confirmação:</span> enviado para seu email
                    </p>
                    <p className="text-sm text-foreground/70">
                      <span className="font-medium text-foreground">Entrega Prevista:</span> 10 a 15 dias úteis
                    </p>
                  </div>

                  <div className="flex flex-col gap-3">
                    <Link
                      href="/minha-conta/pedidos"
                      className="bg-primary text-primary-foreground py-3 rounded-lg font-medium hover:bg-primary/90 transition-colors"
                    >
                      Ver Meus Pedidos
                    </Link>
                    <Link
                      href="/"
                      className="border-2 border-primary text-primary py-3 rounded-lg font-medium hover:bg-primary/5 transition-colors"
                    >
                      Voltar à Loja
                    </Link>
                  </div>
                </div>
              )}
            </div>

            {/* Order Summary Sidebar */}
            <div className="lg:col-span-1">
              <div className="border border-border rounded-lg p-6 space-y-4 sticky top-24">
                <h3 className="font-medium text-foreground">Resumo do Pedido</h3>

                <div className="space-y-3 text-sm border-b border-border pb-4">
                  <div className="flex justify-between text-foreground/70">
                    <span>Subtotal</span>
                    <span>R$ {subtotal.toFixed(2)}</span>
                  </div>
                  <div className="flex justify-between text-foreground/70">
                    <span>Frete</span>
                    <span>{shipping === 0 ? "Grátis" : `R$ ${shipping.toFixed(2)}`}</span>
                  </div>
                  <div className="flex justify-between text-foreground/70">
                    <span>Imposto</span>
                    <span>Calculado</span>
                  </div>
                </div>

                <div className="flex justify-between text-lg font-medium text-foreground">
                  <span>Total</span>
                  <span>R$ {total.toFixed(2)}</span>
                </div>

                <div className="space-y-2 text-xs text-foreground/60 bg-muted p-3 rounded">
                  <p>30 dias de garantia em todos os produtos</p>
                  <p>Rastreamento completo da sua entrega</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <Footer />
      <WhatsAppButton />
    </div>
  )
}
